@import './vars'

body 
    font-family: 'sofia-pro', sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

a
    text-decoration: none

.wrapper
    margin: 0 auto
    width: 1200px
    display: flex

header .wrapper
    padding: 8px 60px 8px 60px 
    justify-content: space-between

header.soft
    background-color: $head   
    color: $text-white
    .links a
        font-weight: 400
        font-size: 13px
        line-height: 24px
        color: $text-white
        margin-right: 15px
    .socials a
        
